@import url(https://fonts.googleapis.com/css?family=Open+Sans);
body {
	margin: 0;
	padding: 0;
	text-align: center;
	background-color: #AA6A39;
	font-family: 'Open Sans', sans-serif;
}

* {
	margin: 0;
	padding: 0;
}

input {
	margin: 10px;
}

.header {
	margin: 1rem;
	margin-top: 0.5rem;
	display: flex;
	flex-direction: column;
	justify-content: space-around;
	align-content: space-around;
	align-items: center;
}

.header h1 {
	margin: 0.5rem;
}

.scroll {
	background-color: #FF9F56;
	overflow-y: scroll;
	border: 3px solid black;
	height: 60vh;
	padding: 10px;
}

.scroll h3 {
	font-size: 1.5rem;
	padding: 0.5rem;
	margin: 0.5rem;
}
.scroll p {
	font-size: 1rem;
	padding: 0.5rem;
	margin: 0.5rem;
}

.scroll ol {
	margin: 0.5rem 1rem 0.5rem 1rem;
	padding: 0.5rem 1rem 0.5rem 1rem;
}

.scroll li {
	/*text-decoration: none;*/
	text-align: start;
	padding: 5px;
}

/* width */
::-webkit-scrollbar {
	width: 12px;
}

/* Track */
::-webkit-scrollbar-track {
	background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
	background: #888;
	border: 1px solid black;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
	background: #555;
}

/* Generate button */
.generateField {
	width: 100%;
	margin: 0.5rem;
}

.generateBtn {
	width: 30%;
	background-color: #FB545D;
	border: none;
	cursor: pointer;
	/*height: 1rem;*/
	font-size: 1rem;
	padding: 7px;
	border-radius: 5px;
}

/* search box */

.search {
	width: 100%;
	margin: 0.5rem;
}

.searchTerm {
	width: 30%;
	border: 2px solid #FB545D;
	padding: 5px;
	border-radius: 5px;
	outline: none;
	color: #000;
	margin: 0;
	font-size: 1rem;
}



